
import {post, get} from ".";

let url = 'http://39.105.8.210:8080';

//教师管理
const addteacherApi = url+'/teacher/addteacher/';
const listteacherApi = url+'/teacher/listteacher/';
const updateteacherApi = url+'/teacher/updateteacher/';
const deleteteacherApi = url+'/teacher/deleteteacher/';

//管理员管理
const addadminApi = url+'/admin/addadmin/';
const getadminlistApi = url+'/admin/getadminlist/';
const resetadminpasswordApi = url+'/admin/resetadminpassword/';
const deleteadminApi = url+'/admin/deleteadmin/';
const getadminbyidApi = url+'/admin/getadminbyid/';
const updateadminApi = url+'/admin/updateadmin/';

//学校组织架构管理
const getacademylistApi = url+'/academy/getacademylist/';
const addacademyApi = url+'/academy/addacademy/';
const renameacademyApi = url+'/academy/renameacademy/';
const deleteacademyApi = url+'/academy/deleteacademy/';
const listenrollmentyearApi = url+'/enrollmentyear/listenrollmentyear/';
const updateenrollmentyearApi = url+'/enrollmentyear/updateenrollmentyear/';
const deleteenrollmentyearApi = url+'/enrollmentyear/deleteenrollmentyear/';
const addenrollmentyearApi = url+'/enrollmentyear/addenrollmentyear/';
const listallenrollmentyearApi = url+'/enrollmentyear/listallenrollmentyear/';
const addclassApi = url+'/class/addclass/';
const deleteclassApi = url+'/class/deleteclass/';
const renameclassApi = url+'/class/renameclass/';
const getallacademylistApi = url+'/academy/getallacademylist/';
const getclassbyacademyidApi = url+'/class/getclassbyacademyid/';

//学生管理
const liststudentApi = url+'/student/liststudent/';
const addstudentApi = url+'/student/addstudent/';
const deletestudentApi = url+'/student/deletestudent/';
const updatestudentApi = url+'/student/updatestudent/';

//学生管理
export const fetchListstudent = (pageNum, pageSize) => (
  post(liststudentApi, {pageNum, pageSize}));
export const fetchAddstudent = (number, name, academyId, enrollmentId, classId) => (
  post(addstudentApi, {number, name, academyId, enrollmentId, classId}));
export const fetchDeletestudent = (id) => (
  post(deletestudentApi, {id}));
export const fetchUpdatestudent = (id, number, name, academyId, enrollmentId, classId) => (
  post(updatestudentApi, {id, number, name, academyId, enrollmentId, classId}));

//学校组织架构管理
export const fetchGetacademylist = (pageNum, pageSize) => (
  post(getacademylistApi, {pageNum, pageSize}));
export const fetchAddacademy = (name) => (
  post(addacademyApi, {name}));
export const fetchRenameacademy = (id, name) => (
  post(renameacademyApi, {id, name}));
export const fetchDeleteacademy = (id) => (
  post(deleteacademyApi, {id}));
export const fetchListenrollmentyear = (pageNum, pageSize) => (
  post(listenrollmentyearApi, {pageNum, pageSize}));
export const fetchUpdateenrollmentyear = (id, enrollmentYear) => (
  post(updateenrollmentyearApi, {id, enrollmentYear}));
export const fetchDeleteenrollmentyear = (id) => (
  post(deleteenrollmentyearApi, {id}));
export const fetchAddenrollmentyear = (enrollmentYear) => (
  post(addenrollmentyearApi, {enrollmentYear}));
export const fetchListallenrollmentyear = () => (
  post(listallenrollmentyearApi, {}));
export const fetchAddclass = (academyId, name) => (
  post(addclassApi, {academyId, name}));
export const fetchDeleteclass = (id) => (
  post(deleteclassApi, {id}));
export const fetchRenameclass = (name, classId) => (
  post(renameclassApi, {name, classId}));
export const fetchGetallacademylist = () => (
  post(getallacademylistApi, {}));
export const fetchGetclassbyacademyid = (academyId) => (
  post(getclassbyacademyidApi, {academyId}));



//教师管理
export const fetchAddteacher = (number, name, academyId) => (
  post(addteacherApi, {number, name, academyId}));
export const fetchListteacher = (pageNum, pageSize) => (
  post(listteacherApi, {pageNum, pageSize}));
export const fetchUpdateteacher = (id, academyId, number, name, password) => (
  post(updateteacherApi, {id, academyId, number, name, password}));
export const fetchDeleteteacher = (id) => (
  post(deleteteacherApi, {id}));

//管理员管理
export const fetchGetadminlist = (pageNum, pageSize) => (
  post(getadminlistApi, {pageNum, pageSize}));
export const fetchAddadmin = (number, name, password) => (
  post(addadminApi, {number, name, password}));
export const fetchResetadminpassword = (id) => (
  post(resetadminpasswordApi, {id}));
export const fetchDeleteadmin = (id) => (
  post(deleteadminApi, {id}));
export const fetchGetadminbyid = (id) => (
  post(getadminbyidApi, {id}));
export const fetchUpdateadmin = (id, number, name, password) => (
  post(updateadminApi, {id, number, name, password}));



const testApi = url+'/test/';
export const fetchTest = (id) => (
  post(testApi, {id}));
