import Vue from 'vue'
import Router from 'vue-router'
import {fetchCheck_token} from "../axios/requests";
import {cookie} from "../utils";


Vue.use(Router)

const router = new Router({
    // mode:'history',
    routes: [
        {
            path: '/',
            redirect: '/dashboard'
        },
        {
            path: '/newcourse/:pid',
            name: 'newcourse',
            component: () => import('../components/page/Snewcourse.vue'),
            meta: { title: '新建教学' }
        },
        {
            path: '/stuworklist',
            name: 'stuworklist',
            component: () => import('../components/page/Sstuworklist.vue'),
            meta: { title: '课程教学' }
        },
        {
            path: '/workdetail',
            name: 'workdetail',
            component: () => import('../components/page/Sworkdetail.vue'),
            meta: { title: '学生作业' }
        },
        {
            path: '/newworks',
            name: 'newworks',
            component: () => import('../components/page/Snewworks.vue'),
            meta: { title: '新建作业' }
        },
        {
            path: '/stuwork',
            name: 'Tstuwork',
            component: () => import('../components/page/Tstuwork.vue'),
            meta: { title: '课程设计' }
        },
        {
            path: '/',
            component: () => import('../components/common/Home.vue'),
            meta: { title: '自述文件' },
            children: [
                {
                    path: '/dashboard',
                    component: () => import('../components/page/Dashboard.vue'),
                    meta: { title: '系统首页' }
                },
                {
                    path: '/course',
                    component: () => import('../components/page/Scourse.vue'),
                    meta: { title: '课程设计' }
                },
                
                {
                    path: '/works',
                    component: () => import('../components/page/Sworks.vue'),
                    meta: { title: '作业列表' }
                },
                
                {
                    path: '/datas',
                    component: () => import('../components/page/Sdatas.vue'),
                    meta: { title: '基础资料' }
                },
                {
                    path: '/message',
                    component: () => import('../components/page/Smessage.vue'),
                    meta: { title: '消息中心' }
                },
                {
                    path: '/stumanage',
                    component: () => import('../components/page/Astumanage.vue'),
                    meta: { title: '学生管理' }
                },
                {
                    path: '/teachermanage',
                    component: () => import('../components/page/Ateachermanage.vue'),
                    meta: { title: '教师管理' }
                },
                {
                    path: '/admin',
                    component: () => import('../components/page/Aadmin.vue'),
                    meta: { title: '管理员管理' }
                },
                {
                    path: '/schoolmanage',
                    component: () => import('../components/page/Aschoolmanage.vue'),
                    meta: { title: '学校组织管理' }
                },
                
                {
                    path: '/stucourse',
                    component: () => import('../components/page/Tstucourse.vue'),
                    meta: { title: '作业详情' }
                },
                {
                    path: '/stumessage',
                    component: () => import('../components/page/Tstumessage.vue'),
                    meta: { title: '消息中心' }
                }
            ]
        },
        {
            path: '/login',
            component: () => import(/* webpackChunkName: "login" */ '../components/page/Login.vue'),
            meta: { title: '学生登录' }
        },{
            path: '/login1',
            component: () => import(/* webpackChunkName: "login" */ '../components/page/Login1.vue'),
            meta: { title: '教师登录' }
        },{
            path: '/login2',
            component: () => import(/* webpackChunkName: "login" */ '../components/page/Login2.vue'),
            meta: { title: '管理员登录' }
        },
        {
            path: '*',
            redirect: '/404'
        }
    ]
})

const getTitle = (title, requireAuth, keywordsType) => {
    document.title = title ? title : '查专利查商标,就上知险查';
};

router.beforeEach(async (to, from, next) => {
  let {title, requireAuth, keywordsType} = to.meta;
  let userToken = cookie.getCookie('access_token') ? cookie.getCookie('access_token') : '';
  if (requireAuth) {
    console.log('路由需要认证');

    if (userToken) {
      try {
        let response = await fetchCheck_token(userToken);
        console.log(response);
        if (response.user_name) {
          console.log('路由认证成功');

          getTitle(title, requireAuth, keywordsType);
          next();
        } else {
          console.log('路由认证失败，身份认证过期,请重新登陆');

          alert('身份认证过期,请重新登陆');
          router.replace({path: '/login'});
        }
      } catch (e) {
        console.log('网络故障');

        console.log(e);
        router.replace({path: '/login'});
      }
    } else {
      alert('您没有登录');
      router.replace({path: '/login'});
    }
  } else {
    console.log('路由不需要认证');

    getTitle(title, requireAuth, keywordsType);
    next();
  }
});

export default router;


